import React, {Component, Fragment} from 'react';
import { Switch, Route, NavLink} from 'react-router-dom';
import NotFound from '../NotFound/NotFound';
import WhyTechOnline from '../WhyTechOnline/WhyTechOnline';
import AboutTutors from '../AboutTutors/AboutTutors';
import OurProgramms from '../OurProgramms/OurProgramms';
import ConsultWithUs from '../ConsultWithUs/ConsultWithUs';
import Home from '../Home/Home';


class Navbar extends Component {
  constructor(props) {
    super(props)

    this.state = {}
  }

  render() {
    return (
      <Fragment>
        <div id="navbar">
        {/* <BrowserRouter> */}
        
            <div className="container">

              <div className="header_top">
                <div className="logo_area">
                  <NavLink exact to='/'><span className="logo">Company Logo</span></NavLink>
                </div>
               

                <ul >
                  <li>
                    <NavLink activeClassName="ListActive" exact to='/'>Home <span></span></NavLink>
                  </li>
                  <li>
                    <NavLink activeClassName="ListActive" exact to='/aboutUs'>About Us <span></span></NavLink>
                  </li>
                  <li>
                    <NavLink activeClassName="ListActive" exact to='/tutors'>Tutors <span></span></NavLink>
                  </li>
                  <li>
                    <NavLink activeClassName="ListActive" exact to='/programms'>Programms <span></span></NavLink>
                  </li>
                  <li>
                    <NavLink activeClassName="ListActive" exact to='/contactUs'>Contact Us <span></span></NavLink>
                  </li>

                </ul>
              </div>

            </div>
            <Switch>
              <Route exact path="/" component={Home}/>
              <Route path="/aboutUs" component={WhyTechOnline}/>
              <Route path="/tutors" component={AboutTutors}/>
              <Route path="/programms" component={OurProgramms}/>
              <Route path="/contactUs" component={ConsultWithUs}/>
              <Route component={NotFound}/>
            </Switch>
            
          {/* </BrowserRouter> */}
         
    

        </div>
      </Fragment>
    )
  }
}

export default Navbar
